html{
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(rgb(14, 92, 21), rgb(30, 187, 114));
  background-image: linear-gradient(rgb(14, 92, 21), rgb(30, 187, 114));
  background-attachment: fixed;
  height: 100%;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}

.app{
  margin: 0px;
  padding: 0px;
  height: 100%;
}
.header{
    background-color: rgb(29, 177, 41);
    width: 100%;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: white;
}
.header-logo{
    font-weight: bolder;
    font-size: 17px;
    margin-left: 15px;
}
.header-info{
    font-weight: 500;
    font-size: 15px;
    margin-left: auto;
    margin-right: 15px;
    
}

.se{
    width: 100%;
    height: calc(100% - 60px - 30px);
    padding: 15px;
    display: -webkit-flex;
    display: flex;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.se-content{
    background-color: white;
    float: 1;
    width: calc(100% - 30px);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.se-input{
    width: calc(100% - 30px);
    font-weight: bold;
    background-color: gainsboro;
    padding: 15px;
    display: -webkit-flex;
    display: flex;
}
.se-input button{
    background-color: rgb(29, 177, 41);
    border: none;
    margin: -15px;
    padding: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    font-size: 18px;
    color: white;
    margin-left: 15px;
}
.se-input button:hover{
    cursor: pointer;
}
.se-input button:active{
    background-color: rgb(14, 94, 21);
}
.se table{
    width: 100%;
}
.se .table{
    -webkit-flex:1 1;
            flex:1 1;
}
.se table td{
    text-align: center;
    width: 33.33%
}
.se .rodape{
    background-color: gainsboro;
    width: calc(100% - 15px);
    height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: black;
    padding-left: 15px;
    font-weight: bolder;
}
.se .rodape .pagination{
    margin-left: auto;
    margin-right: 15px;
}   
.se .rodape .pagination button{
    background-color: rgba(0,0,0,0);
    border: none;
    margin-left: 5px;
}
.se .rodape .pagination button:hover{
    cursor: pointer;
}
.it-inp{
    position: relative;
    margin: auto;
    width: 100%;
}
.it-label{
    position: absolute;
    top: -5px;
    left: 0px;
    font-size: 18px;
    font-weight: 500;
    color: #9098A9;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    cursor: text;
}
.it-input{
    -webkit-appearance: none;
    width: 100%;
    border: 0;
    font-family: inherit;
    padding: 3px 0;
    height: 18px;
    font-size: 16px;
    font-weight: 500;
    border-bottom: 2px solid #C8CCD4;
    background: none;
    border-radius: 0;
    color: #223254;
    -webkit-transition: all .15s ease;
    transition: all .15s ease;
}
.it-input:hover{
    background: rgba(#223254,.03);
}
.it-input:not(:placeholder-shown)
    +span{
        color: #5A667F;
        -webkit-transform: translateY(-12px) scale(0.65);
                transform: translateY(-12px) scale(0.65);
    
}
.it-input:focus{
    background: none;
    outline: none; 
}
.it-inp input:focus+span{
    color: #0077FF;
    -webkit-transform: translateY(-12px) scale(0.65);
            transform: translateY(-12px) scale(0.65);
}
.it-border{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: #0077FF;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transition: all .15s ease;
    transition: all .15s ease;
}
.auth-global{
    display: -webkit-flex;
    display: flex;
    height: 100%;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
}
.auth-container{
    background-color: white;
    width: 350px;
    height: 250px;
    margin: auto;
}
.auth-inputContainer{
    padding: 15px;
    padding-top: 30px;
}
.auth-button{
    width: 100%;
    height: 50px;
    margin-top: auto;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    font-size: 18px;
    color: white;
    background-color: rgb(29, 177, 41);
    border: none;
}
.auth-button:hover{
    cursor: pointer;
}
.auth-button:active{
    background-color: rgb(14, 94, 21);
}

